
body { margin: 0;  padding: 0;  font-family: 'Open Sans', sans-serif; } 

$imagePath: "../img";


.homebanner{ background:url("#{$imagePath}/landing-bg.jpg") center no-repeat; background-size:cover; height:100vh; width: 100%; position:relative; 
	img{     z-index: 2;}
}

section.establish-note{     
		 padding: 6rem 0;
		 @media(max-width: 767px){
				padding: 3rem 0;
			}
}

section.our-services{ padding:10px 0 5rem; }

section.industries{ background-color: darken(white, 4%); padding:10px 0 5rem; }

section.our-approch{ @extend section.our-services }

section.client-say{ @extend section.industries; 
	.carousel{ padding: 35px 35px 70px; background-color: #fff; 
		@media(min-width: 768px){
			    margin: auto;     width: 80%;
		}
		.carousel-inner{}
	}
	.carousel-indicators{     right: inherit;      bottom: 15px;
		li{ background-color: darken(white, 9%);  }
		.active{ background-color: darken(gray, 1%);  }
	}
	
}

